import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import RedirectRoute from "./RedirectRoute";
import Home from "./Home";

const App = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path={"/"}>
          <Home />
        </Route>
        <RedirectRoute path={"/ref/*"} />
      </Switch>
    </BrowserRouter>
  );
};

export default App;
